import React, { ChangeEvent, useState } from 'react'
import s from './HW11.module.css'
import s2 from '../../s1-main/App.module.css'
import { restoreState } from '../hw06/localStorage/localStorage'
import SuperRange from './common/c7-SuperRange/SuperRange'

/*
* 1 -     
* 2 -      change
* 3 -      
* */

function HW11() {
    // for autotests //   //      , 
    //     
    const [value1, setValue1] = useState(restoreState<number>('hw11-value', 0))
    const [value2, setValue2] = useState(restoreState<number>('hw11-value1', 100))

    const change1 = (event: Event, value: number | number[]) => {
        //   //    -     useState,   
        if (Array.isArray(value)) {
            setValue1(value[0])
            setValue2(value[1])
        } else {
            setValue1(value)
        }
    }

    const minDistance = 1;


    return (
        <div id={'hw11'} className={s.main_container}>
            <div className={s2.hwTitle}>Homework #11</div>

            <div className={s2.hw}>
                <div className={s.container}>
                    <div className={s.wrapper}>
                        <span id={'hw11-value'} className={s.number}>{value1}</span>
                        <div className={s.slider}>
                            <SuperRange
                                id={'hw11-single-slider'}
                                //    value1  //  
                                onChange={change1}
                                value={value1}
                            />
                        </div>
                    </div>
                    <div className={s.wrapper}>
                        <span id={'hw11-value-1'} className={s.number}>{value1}</span>
                        <div className={s.slider}>
                            <SuperRange
                                id={'hw11-double-slider'}
                                //    value1/2  //  
                                onChange={change1}
                                value={[value1, value2]}
                            />
                        </div>

                        <span id={'hw11-value-2'} className={s.number}>{value2}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HW11
