import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    //const [date, setDate] = useState<Date>(new Date)
    const [show, setShow] = useState<boolean>(false)


    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        stop();
        const id = window.setInterval(()=>{
            setDate(new Date())
        }, 1000)
        setTimerId(id);
    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        clearInterval(timerId);
        setTimerId(undefined);
    }

    const onMouseEnter = () => { //   //     
        setShow(!show);
    }
    const onMouseLeave = () => { //   //      
        setShow(!show);
    }

    {/*
    const stringTime = `${date.getHours()<10 ? '0' + date.getHours() : date.getHours()}
                                        :${date.getMinutes()<10 ? '0' + date.getMinutes() : date.getMinutes()}
                                        :${date.getSeconds()<10 ? '0' + date.getSeconds() : date.getSeconds()}` || <br/>
    */}
    const stringTime = date.toLocaleTimeString() || <br/>
    // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  


    const stringDate = `${date.getDate()<10 ? '0' + date.getDate() : date.getDate()}.${date.getMonth() < 10 ? '0'+(date.getMonth()+1): (date.getMonth()+1)}.${date.getFullYear()}` || <br/>
    //const stringDate = date.toLocaleDateString() || <br/>
    // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  


    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const weekDays = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
    const stringDay = `${weekDays[date.getDay()]}` || <br/> //  

    const month = ["January","February","March","April","May","June","July","August","September","October","November","December"];
    const stringMonth = `${month[date.getMonth()]}` || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={timerId !== undefined} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={timerId === undefined} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
